/* ページネーション全体のスタイル */
.wp-pagenavi {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi {
    gap: 0.5rem;
  }
}

/* ====== ページ番号リンクのスタイル ====== */
.wp-pagenavi .page {
  height: 1.875rem;
  min-width: 1.875rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  color: #1e1e1e;
  font-size: 1rem;
  line-height: normal;
  transition: background 0.2s ease-out;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .page {
    height: 1.75rem;
    min-width: 1.75rem;
    font-size: 0.8125rem;
  }
}

.wp-pagenavi .page:hover {
  background: #eef0f1;
  border-radius: 0.3125rem;
}

/* ====== 現在のページ番号 (総ページ数) ====== */
.wp-pagenavi .current {
  height: 1.875rem;
  min-width: 1.875rem;
  padding-inline: 0.1875rem;
  display: grid;
  place-items: center;
  color: #1e1e1e;
  font-size: 1rem;
  line-height: normal;
  background: #eef0f1;
  border-radius: 0.3125rem;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .current {
    height: 1.75rem;
    min-width: 1.75rem;
    font-size: 0.8125rem;
  }
}

/* ====== 現在のページの順番 ====== */
.wp-pagenavi .pages {
  display: none;
}

/* ====== 最初の一覧ページに飛ぶリンク ====== */
.wp-pagenavi .first {
  margin-right: 1.125rem;
  font-size: 1.5rem;
  display: grid;
  place-items: center;
  position: relative;
  width: 1rem;
  height: 1rem;
  transition: opacity 0.2s ease-out;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .first {
    margin-right: 0.625rem;
    font-size: 0.8125rem;
    width: 0.875rem;
    height: 0.875rem;
  }
}

.wp-pagenavi .first:hover {
  opacity: 0.4;
}

.wp-pagenavi .first::before,
.wp-pagenavi .first::after {
  content: "";
  width: 1rem; /* 矢印の太さ */
  height: 1rem; /* 矢印の高さ */
  border-top: 1.5px solid #333333;
  border-left: 1.5px solid #333333;
  position: absolute;
  top: 50%;
  left: 0;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .first::before,
  .wp-pagenavi .first::after {
    width: 0.875rem;
    height: 0.875rem;
  }
}

.wp-pagenavi .first::before {
  transform: translate(0, -50%) rotate(-45deg);
}

.wp-pagenavi .first::after {
  transform: translate(-5px, -50%) rotate(-45deg);
}

/* ====== 最後の一覧ページに飛ぶリンク ====== */
.wp-pagenavi .last {
  margin-left: 1.125rem;
  font-size: 1.5rem;
  display: grid;
  place-items: center;
  position: relative;
  width: 1rem;
  height: 1rem;
  transition: opacity 0.2s ease-out;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .last {
    margin-left: 0.625rem;
    font-size: 0.8125rem;
    width: 0.875rem;
    height: 0.875rem;
  }
}

.wp-pagenavi .last:hover {
  opacity: 0.4;
}

.wp-pagenavi .last::before,
.wp-pagenavi .last::after {
  content: "";
  width: 1rem; /* 矢印の太さ */
  height: 1rem; /* 矢印の高さ */
  border-top: 1.5px solid #333333;
  border-right: 1.5px solid #333333;
  position: absolute;
  top: 50%;
  left: 0;
}
@media screen and (max-width: 767px) {
  .wp-pagenavi .last::before,
  .wp-pagenavi .last::after {
    width: 0.875rem;
    height: 0.875rem;
  }
}

.wp-pagenavi .last::before {
  transform: translate(0, -50%) rotate(45deg);
}

.wp-pagenavi .last::after {
  transform: translate(-5px, -50%) rotate(45deg);
}

